import { useCallback } from "react"
import { useLocation, useNavigate } from "react-router-dom"

const useHashParams = (): [Record<string, string>, (params: Record<string, string>) => void] => {
  const location = useLocation()
  const navigate = useNavigate()

  const hash = location.hash.slice(1)
  const hashParams = Object.fromEntries(new URLSearchParams(hash))

  const setHashParams = useCallback(
    (params: Record<string, string>) => {
      const paramsString = new URLSearchParams(params).toString()
      navigate({ hash: `?${paramsString}`, search: location.search })
    },
    [location.search, navigate],
  )

  return [hashParams, setHashParams]
}

export { useHashParams }
